import axios from 'axios'

const baseUrlApi = process.env.VUE_APP_BASE_URL_API

// axios
const service = axios.create({
  baseURL: baseUrlApi, // apibase_url
  timeout: 10000 // 
})

// request
service.interceptors.request.use(config => {
  config.headers['Content-Type'] = config.headers['Content-Type'] || 'application/json;charset=UTF-8'
  // config.headers['Authorization'] = getToken()
  return config
}, error => {
  // Do something with request error
  // console.log(error) // for debug
  Promise.reject(error)
})

// respone
service.interceptors.response.use(response => {
  /* if (Number(response.status) === 200 && response.data && (Number(response.data.code) === 401 || Number(response.data.code) === 402)) {
      window.location.href = '/#/login'
  } */
  return response
}, error => {
  // console.log('err' + error) // for debug
  return Promise.reject(error)
})

export const ajax = service

export default {
  install (Vue, options) {
    /**
     * ajax POST
     * @param   url      
     * @param   params   
     * @return  Promise
     */
    Vue.prototype.$post = (url, params, config) => {
      const postData = params || {}
      return new Promise((resolve, reject) => {
        service.post(url, postData, config || {}).then(res => {
          const status = parseInt(res.status)
          const statusText = res.statusText
          if (status === 200) {
            const code = parseInt(res.data.code)
            if (code === 0) {
              resolve(res.data)
            } else if (code === 401 || code === 402) {
              // 
            } else {
              reject(new Error(JSON.stringify({
                code: code,
                message: res.data.msg
              })))
            }
          } else {
            reject(new Error(JSON.stringify({
              code: status,
              message: statusText
            })))
          }
        }).catch(error => {
          reject(error)
        })
      })
    }

    /**
     * ajax GET
     * @param   url      
     * @param   params   
     * @return  Promise
     */
    Vue.prototype.$get = (url, params) => {
      const postData = params || {}
      return new Promise((resolve, reject) => {
        service.get(url, postData).then(res => {
          const status = parseInt(res.status)
          const statusText = res.statusText
          if (status === 200) {
            const code = parseInt(res.data.code)
            if (code === 0) {
              resolve(res.data)
            } else if (code === 401 || code === 402) {
              // 
            } else {
              reject(new Error(JSON.stringify({
                code: code,
                message: res.data.msg
              })))
            }
          } else {
            reject(new Error(JSON.stringify({
              code: status,
              message: statusText
            })))
          }
        }).catch(error => {
          reject(error)
        })
      })
    }
  }
}
