import ToastComponent from './toast.vue'

const Toast = {}

Toast.install = function (Vue) {
  const ToastConstructor = Vue.extend(ToastComponent)
  const instance = new ToastConstructor()

  let timer = null

  instance.$mount(document.createElement('div'))
  document.body.appendChild(instance.$el)

  Vue.prototype.$showToast = (msg, duration, icon = 'icon-checked') => {
    instance.msg = msg
    instance.icon = icon
    instance.show = true

    if (timer) {
      clearTimeout(timer)
    }

    duration = parseInt(duration)
    if (duration) {
      timer = setTimeout(() => {
        instance.show = false
      }, duration)
    }
  }

  Vue.prototype.$hideToast = () => {
    instance.show = false
  }

  Vue.prototype.$showToastError = (error) => {
    let msg = ''
    if (error instanceof Error) {
      const message = error.message
      try {
        const msgJSON = JSON.parse(message)
        if (msgJSON.message) {
          msg = msgJSON.message
        }
      } catch (e) {
        msg = error
      }
    } else {
      msg = error
    }
    if (timer) {
      clearTimeout(timer)
    }
    instance.msg = msg
    instance.icon = ''
    instance.show = true

    timer = setTimeout(() => {
      instance.show = false
    }, 3000)
  }
}

export default Toast
