import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)
const wsHost = 'ws://' + window.location.hostname + ':8083/'
export default new Vuex.Store({
  state: {
    userInfo: null, // 
    driverInfo: null, // 
    startInfo: {}, // 
    endInfo: {}, // 
    userSocket: null, // 
    userMsg: [], // 
    userSocketStatus: false, // 
    driverSocket: null, // 
    driverMsg: [], // 
    driverSocketStatus: false // 
  },
  getters: {
    getUserSocket: state => {
      return state.userSocket
    },
    getUserMsg: state => {
      return state.userMsg
    },
    getUserSocketStatus: state => {
      return state.userSocketStatus
    },
    getStartInfo: state => {
      return state.startInfo
    },
    getEndInfo: state => {
      return state.endInfo
    },
    getDriverSocket: state => {
      return state.driverSocket
    }
  },
  mutations: {
    SET_USER_SOCKET: (state, userSocket) => {
      state.userSocket = userSocket
    },
    SET_USER_SOCKET_STATUS: (state, userSocketStatus) => {
      state.userSocket = userSocketStatus
    },
    SET_USER_MSG: (state, userMsg) => {
      state.userMsg = userMsg
    },
    SET_DRIVER_SOCKET: (state, driverSocket) => {
      state.driverSocket = driverSocket
    },
    SET_DRIVER_SOCKET_STATUS: (state, driverSocketStatus) => {
      state.driverSocketStatus = driverSocketStatus
    },
    SET_DRIVER_MSG: (state, driverMsg) => {
      state.driverMsg = driverMsg
    },
    SET_START_INFO: (state, startInfo) => {
      state.startInfo = startInfo
    },
    SET_END_INFO: (state, endInfo) => {
      state.endInfo = endInfo
    }
  },
  actions: {
    creatUserWS ({ commit }) {
      return new Promise((resolve, reject) => {
        const ws = new WebSocket(wsHost + 'websocket/1')
        ws.onopen = function () {
          commit('SET_USER_SOCKET', ws)
          commit('SET_USER_SOCKET_STATUS', true)
          resolve(ws)
        }
        ws.onclose = function () {
          Vue.prototype.$showToast('', 3000)
          commit('SET_USER_SOCKET_STATUS', false)
        }
        ws.onerror = function (err) {
          reject(err)
        }
      })
    },
    creatDriverWS ({ commit }) {
      return new Promise((resolve, reject) => {
        const ws = new WebSocket(wsHost + 'websocket/2')
        ws.onopen = function () {
          commit('SET_DRIVER_SOCKET', ws)
          commit('SET_DRIVER_SOCKET_STATUS', true)
          resolve(ws)
        }
        ws.onclose = function () {
          Vue.prototype.$showToast('', 3000)
          commit('SET_DRIVER_SOCKET_STATUS', false)
        }
        ws.onerror = function (err) {
          reject(err)
        }
      })
    }
  }
})
