import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/Home.vue'
import Empty from './views/Empty.vue'
import Driver from './views/Driver.vue'
import DriverOn from './views/DriverOn.vue'
import DriverRoad from './views/DriverRoad.vue'
import DriverPay from './views/DriverPay.vue'

Vue.use(Router)

export default new Router({
  mode: 'hash',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/user',
      name: 'home',
      component: Home
    },
    {
      path: '/driver',
      name: 'driver',
      redirect: '/driver/index',
      component: Empty,
      children: [{
        path: '/driver/index',
        name: 'driver-index',
        component: Driver
      }, {
        path: '/driver/on',
        name: 'driver-on',
        component: DriverOn
      }, {
        path: '/driver/ing',
        name: 'driver-road',
        component: DriverRoad
      }, {
        path: '/driver/pay',
        name: 'driver-pay',
        component: DriverPay
      }]
    }
  ]
})
