<template>
  <div class="home">
    <div class="amap-container" id="container"></div>
    <div class="user-header">
      <div class="user">
        <button @click="showSidebarNav = true"><i class="iconfont iconuser"></i></button>
      </div>
      <div class="title" v-if="type == 1"></div>
      <div class="title" v-if="type == 2"></div>
      <div class="title" v-if="type == 3"></div>
      <div class="tv">
        <button><i class="iconfont icontv"></i></button>
      </div>
    </div>
    <div class="form-area">
      <div class="service-form">
        <ul>
          <li>
            <i></i>
            <input placeholder="" v-model="startInfo.formattedAddress" readonly>
          </li>
          <li>
            <i></i>
            <input placeholder="" v-model="endInfo.address" readonly>
          </li>
          <li>
            <button type="button" class="btn-primary" v-if="type == 1" @click="arrive"></button>
            <button type="button" class="btn-primary" v-if="type == 2" @click="pickup"></button>
            <button type="button" class="btn-primary" v-if="type == 3" @click="pay"></button>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>
<script>
import { mapGetters } from 'vuex'
import startPoint from '@/assets/start_point.png'
import endPoint from '@/assets/end_point.png'
export default {
  data () {
    return {
      amap: null,
      type: 1,
      // startInfo: {},
      // endInfo: {},
      startPoint: null,
      endPoint: null,
      carMarker: null,
      drivingPois: [],
      carMarker: null,
      polylines: null,
      passedPolylines: null
    }
  },
  computed: {
    ...mapGetters({
      ws: 'getDriverSocket',
      startInfo: 'getStartInfo',
      endInfo: 'getEndInfo'
    })
  },
  created () {
    // console.log(JSON.stringify(this.$startInfo))
    // console.log(JSON.stringify(this.$endInfo))
  },
  mounted () {
    const me = this
    // this.startInfo = {"type":"complete","info":"SUCCESS","status":1,"dCa":"jsonp_511458_","position":{"P":40.0001,"Q":116.48703,"lng":116.48703,"lat":40.0001},"message":"Get geolocation time out.Get ipLocation success.Get address success.","location_type":"ip","accuracy":null,"isConverted":true,"addressComponent":{"citycode":"010","adcode":"110105","businessAreas":[{"name":"","id":"110105","location":{"P":39.996171,"Q":116.47029299999997,"lng":116.470293,"lat":39.996171}},{"name":"","id":"110105","location":{"P":39.981321,"Q":116.49289099999999,"lng":116.492891,"lat":39.981321}}],"neighborhoodType":"","neighborhood":"","building":"","buildingType":"","street":"","streetNumber":"1","province":"","city":"","district":"","township":""},"formattedAddress":".A","roads":[],"crosses":[],"pois":[]}

    // this.endInfo = {"id":"B0FFJIWEC4","name":"()","type":";;","location":{"P":40.002232,"Q":116.48657100000003,"lng":116.486571,"lat":40.002232},"address":"8","tel":"010-64772718","distance":240,"shopinfo":"0"}

    me.amap = new AMap.Map('container', {
      zoom: 15,
      resizeEnable: false,
      dragEnable: false
    })

    me.startPoint = new AMap.Marker({
      position: [this.startInfo.position.lng, this.startInfo.position.lat],
      icon: startPoint,
      offset: new AMap.Pixel(-13, -30)
      // draggable: true,
      // cursor: 'move'
    })
    me.startPoint.setMap(me.amap)
    me.startPoint.setLabel({
      offset: new AMap.Pixel(0, -42),
      content: '<div class="mark-label"></div>',
      direction: 'center'
    })

    me.endPoint = new AMap.Marker({
      position: [me.endInfo.location.lng, me.endInfo.location.lat],
      icon: endPoint,
      offset: new AMap.Pixel(-13, -30)
    })
    me.endPoint.setMap(me.amap)
    me.endPoint.setLabel({
      offset: new AMap.Pixel(0, -42),
      content: '<div class="mark-label mark-label-red"></div>',
      direction: 'center'
    })

    me.amap.setFitView()
  },
  methods: {
    arrive () {
      this.type = 2
      this.ws.send(JSON.stringify({
        to: 1,
        type: '',
        data: {}
      }))
    },
    pickup () {
      const me = this
      this.type = 3
      this.ws.send(JSON.stringify({
        to: 1,
        type: '',
        data: {}
      }))
      me.getDrivingPois().then(() => {
        // 
        me.creatCarMarker().then(marker => {
          marker.setMap(me.amap)
          me.polyline()
        })
      })
    },
    pay () {
      this.$router.push('/driver/pay')
    },
    // 
    getDrivingPois () {
      const me = this
      return new Promise((resolve, reject) => {
        AMap.service('AMap.Driving', function () {
          let driving = new AMap.Driving({
            hideMarkers: true
          })
          driving.search(new AMap.LngLat(me.startInfo.position.lng, me.startInfo.position.lat), new AMap.LngLat(me.endInfo.location.lng, me.endInfo.location.lat), function (status, result) {
            if (status === 'complete') {
              let drivingPois = []
              if (result.routes && result.routes[0] && result.routes[0].steps) {
                result.routes[0].steps.forEach(item => {
                  drivingPois = drivingPois.concat(item.path)
                })
              }
              me.drivingPois = drivingPois
              resolve(drivingPois)
            } else {
              reject(result)
            }
          })
        })
      })
    },
    // 
    creatCarMarker () {
      const me = this
      return new Promise(resolve => {
        if (me.carMarker) {
          me.carMarker.setPosition(me.startInfo.position)
        } else {
          me.carMarker = new AMap.Marker({
            position: me.startInfo.position,
            icon: 'https://webapi.amap.com/images/car.png',
            offset: new AMap.Pixel(-26, -13),
            autoRotation: true,
            angle: -90
          })
        }
        resolve(me.carMarker)
      })
    },
    // 
    polyline () {
      const me = this
      return new Promise(resolve => {
        me.polylines = new AMap.Polyline({
          map: me.amap,
          path: me.drivingPois,
          showDir: true,
          strokeColor: '#28F',
          strokeWeight: 6
        })
        me.passedPolylines = new AMap.Polyline({
          map: me.amap,
          strokeColor: '#AF5',
          strokeWeight: 6
        })
        me.amap.setFitView(me.polylines)
        me.carMarker.on('moving', function (e) {
          me.passedPolylines.setPath(e.passedPath)
        })
        me.carMarker.moveAlong(me.drivingPois, 200)
      })
    }
  }
}
</script>
<style>
.home {
  width: 100%;
  height: 100%;
}
.amap-container {
  width: 100vw;
  height: 100vh;
  position: relative;
  z-index: 1;
}
.form-area {
  width: 96%;
  position: absolute;
  left: 2%;
  bottom: 0;
  z-index: 2;
}
.amap-marker-label {
  border: 0;
  background-color: #ffffff;
  line-height: 36px;
  border-radius: 16px;
  padding: 0 16px;
  box-sizing: border-box;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.3);
  color: #00b48d;
}
.mark-label-red {
  color: red;
}
.service-type {
  float: left;
  clear: both;
  background-color: rgba(255,255,255, 0.9);
  border-radius: 4px;
  border-bottom: 1px #dddddd solid;
  box-shadow: 0 0 2px rgba(0, 0, 0, 0.1);
  margin-bottom: 6px;
  overflow: hidden;
}
.service-type ul, .service-type ul li {
  display: flex;
  margin: 0;
  padding: 0;
  list-style: none;
}
.service-type ul li {
  padding: 0 10px;
}
.service-type ul li a {
  color: #666666;
  text-decoration: none;
  font-size: 1.4rem;
  display: block;
  line-height: 3;
  padding: 3px 10px 0;
  border-bottom: 3px #ffffff solid;
}
.service-type ul li.current a { border-color: #00b48d; }

.service-form {
  width: 100%;
  float: left;
  clear: both;
  background-color: rgba(255,255,255, 0.9);
  border-radius: 4px;
  box-sizing: border-box;
  box-shadow: 0 0 2px rgba(0, 0, 0, 0.1);
  margin-bottom: 6px;
  padding: 10px 0;
}
.service-form ul, .service-form ul li {
  margin: 0;
  padding: 0;
  list-style: none;
}
.service-form ul li {
  display: flex;
  align-items: center;
  padding: 0 10px;
}
.service-form ul li input {
  height: 40px;
  border: 0;
  outline: none;
  flex: 1;
  font-size: 15px;
  background: none;
}
.service-form ul li i {
  display: block;
  width: 8px;
  height: 8px;
  border-radius: 100%;
  background-color: #00b48d;
  margin: 0 15px;
}
.service-form ul li:nth-child(2) i {
  background-color: palevioletred;
}
.service-form ul li:nth-child(3) {
  padding: 15px 10px 5px;
}
.btn-primary {
  width: 100%;
  line-height: 3.2;
  background-color: #00b48d;
  border: 0;
  outline: none;
  color: #ffffff;
  border-radius: 2px;
  font-size: 15px;
}
.btn-primary:hover {
  background-color: #00b68e;
}
.btn-primary:active {
  box-shadow: 0 0 20px 0 #00745a inset;
}

.seach-autocomplete {
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0;
  top: 0;
  background: #f5f5f5;
  padding: 2px;
  box-sizing: border-box;
  z-index: 10;
  overflow: auto;
}
.seach-autocomplete li {
  border-bottom: 1px #eeeeee solid;
  text-align: left;
  display: flex;
  align-items: center;
  padding: 15px 10px;
  font-size: 15px;
  cursor: pointer;
  background-color: #ffffff;
}
.seach-autocomplete li i {
  width: 6px;
  height: 6px;
  border-radius: 100%;
  background-color: #dddddd;
  margin: 0 15px;
}
.seach-autocomplete li .name {
  display: block;
}
.seach-autocomplete li .address {
  display: block;
  color: #999999;
  font-size: 12px;
}
.fade-enter-active, .fade-leave-active {
  transition: top .5s;
}
.fade-enter, .fade-leave-to .fade-leave-active {
  top: 100%;
}

.user-header {
  width: 100%;
  position: absolute;
  background-color: #ffffff;
  left: 0;
  top: 0;
  z-index: 3;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.1);
  display: flex;
  align-items: center;
  line-height: 56px;
}
.user-header .title {
  flex: 1;
  font-size: 16px;
}
.user-header button {
  background: none;
  border: 0;
  color: #00b48d;
  padding: 0 20px;
  outline: none;
  cursor: pointer;
  display: flex;
  height: 100%;
  line-height: 56px;
}
.user-header .tv .iconfont {
  font-size: 30px;
}
.user-header .user .iconfont {
  font-size: 20px;
}
.user-nav {
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0;
  top: 0;
  z-index: 5;
  background-color: rgba(0, 0, 0, 0.3);
}
.user-nav-box {
  width: 80%;
  height: 100%;
  background-color: #ffffff;
  position: absolute;
  left: 0;
  top: 0;
}
.user-nav .user-info {
  height: 24%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  border-bottom: 1px #eeeeee solid;
  font-size: 16px;
}
.user-nav .user-info i {
  font-size: 32px;
  color: #999999;
  margin-bottom: 5px;
}
.user-nav .user-nav-list {
  text-align: left;
  padding-top: 20px;
}
.user-nav .user-nav-list li {
  padding: 15px 30px;
  font-size: 16px;
  display: flex;
  align-items: center;
}
.user-nav .user-nav-list li i {
  color: #00b48d;
  font-size: 20px;
}
.user-nav .user-nav-list li div {
  width: 40px;
}
.user-nav .user-nav-list li span {
  flex: 1;
}

.fade-bg-enter-active, .fade-bg-leave-active {
  transition: opacity .5s;
}
.fade-bg-enter, .fade-bg-leave-to .fade-bg-leave-active {
  opacity: 0;
}

.fade-content-enter-active, .fade-content-leave-active {
  transition: left .5s ease-in;
}
.fade-content-enter, .fade-content-leave-to .fade-content-leave-active {
  left: -100%;
}
</style>
