<template>
  <div id="message" class="message-box" v-show="show">
    <div class="message-mask"></div>
    <div class="message-content">
      <div class="message-title">{{title}}</div>
      <div class="message-main"><span class="message-main-inner">{{content}}</span></div>
      <div class="message-btns">
        <button v-show="showCancel" v-on:click="cancel">{{cancelText}}</button>
        <button class="primary" v-on:click="confirm" v-bind:style="{ width: showCancel ? '50%' : '100%' }">{{confirmText}}</button>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data () {
    return {
      title: '',
      content: '',
      showCancel: true,
      cancelText: '',
      confirmText: '',
      show: false,
      cancelCallBack: null,
      confirmCallBack: null
    }
  },
  methods: {
    hide () {
      this.show = false
      this.cancelCallBack = null
      this.confirmCallBack = null
    },
    cancel () {
      if (typeof this.cancelCallBack === 'function') {
        this.cancelCallBack()
      }
      setTimeout(() => {
        this.hide()
      })
    },
    confirm () {
      if (typeof this.confirmCallBack === 'function') {
        this.confirmCallBack()
      }
      setTimeout(() => {
        this.hide()
      })
    }
  }
}
</script>
<style scoped>
.message-box {
  width: 100vw;
  height: 100vh;
  position: fixed;
  left: 0;
  top: 0;
  z-index: 1000;
  display: flex;
  align-items: center;
  align-content: center;
  justify-content: center;
}
.message-mask {
  width: 100%;
  height: 100%;
  background-color: rgba(0,0,0,0.6);
  position: absolute;
  left: 0;
  top: 0;
  z-index: 1;
}
.message-content {
  width: 30rem;
  background-color: #ffffff;
  border-radius: 5px;
  position: relative;
  z-index: 2;
  overflow: hidden;
}
.message-title {
  font-size: 1.6rem;
  text-align: center;
  padding: 2.6rem 3.2rem 1rem;
  color: #333333;
}
.message-main {
  text-align: center;
  padding: 0 3.2rem 1.6rem;
  color: #777777;
  font-size: 1.4rem;
  line-height: 1.3;
}
.message-main-inner {
  display: inline-block;
  text-align: left;
}
.message-btns {
  display: flex;
  border-top: 1px solid #eaecef;
}
.message-btns > button {
  width: 50%;
  height: 4.8rem;
  box-sizing: border-box;
  background: none;
  border: 0;
  cursor: pointer;
  font-size: 1.6rem;
  outline: none;
  color: #666666;
}
.message-btns > button:active {
  background-color: #f5f5f5;
}
.message-btns > button:last-child {
  border-left: 1px solid #eaecef;
}
.message-btns .primary {
  color: #00b48d;
}
</style>
