import MessageComponent from './message.vue'

const Message = {}

Message.install = function (Vue) {
  const MessageConstructor = Vue.extend(MessageComponent)
  const instance = new MessageConstructor()

  instance.$mount(document.createElement('div'))
  document.body.appendChild(instance.$el)

  Vue.prototype.$showMessage = (opts) => {
    opts = opts || {}

    instance.title = opts.title || ''
    instance.content = opts.content || ''
    instance.showCancel = opts.showCancel !== false
    instance.cancelText = opts.cancelText || ''
    instance.confirmText = opts.confirmText || ''
    instance.show = true
    instance.cancelCallBack = opts.cancelCallBack || null
    instance.confirmCallBack = opts.confirmCallBack || null
  }

  Vue.prototype.$hideMessage = () => {
    instance.show = false
  }
}

export default Message
