<template>
  <div class="home">
    <div class="amap-container" id="container"></div>
    <div class="user-header">
      <div class="user">
        <button @click="showSidebarNav = true"><i class="iconfont iconuser"></i></button>
      </div>
      <div class="title">Online Taxi</div>
      <div class="tv">
        <button><i class="iconfont icontv"></i></button>
      </div>
    </div>
    <div class="form-area">
      <div class="service-type">
        <ul>
          <li class="current">
            <a href="javascript:;"></a>
          </li>
          <li>
            <a href="javascript:;"></a>
          </li>
          <li>
            <a href="javascript:;"></a>
          </li>
          <li>
            <a href="javascript:;"></a>
          </li>
          <li>
            <a href="javascript:;"></a>
          </li>
        </ul>
      </div>
      <div class="service-form">
        <ul>
          <li>
            <i></i>
            <input placeholder="" v-model="startAddr">
          </li>
          <li>
            <i></i>
            <input placeholder="" v-model="endAddr" @focus="showAutocomplete = true" readonly>
          </li>
          <li>
            <button type="button" class="btn-primary" @click="callCar" v-if="on"></button>
          </li>
        </ul>
      </div>
    </div>
    <transition name="fade">
      <div class="seach-autocomplete" v-show="showAutocomplete">
        <div class="seach-autocomplete-list">
          <ul>
            <li v-for="(item, index) in destinationList" :key="index" @click="selectDestination(index)"><i></i><div class="info"><span class="name">{{item.name}}</span><span class="address">{{item.address}}</span></div></li>
          </ul>
        </div>
      </div>
    </transition>
    <transition name="fade-bg">
      <div class="user-nav" v-show="showSidebarNav" @click.self="showSidebarNav = false">
        <transition name="fade-content">
          <div class="user-nav-box" v-show="showSidebarNav">
            <div class="user-info">
              <i class="iconfont iconuser"></i>
              <div class="username"></div>
            </div>
            <div class="user-nav-list">
              <ul>
                <li><div><i class="iconfont iconbag"></i></div><span></span></li>
                <li><div><i class="iconfont iconorder"></i></div><span></span></li>
                <li><div><i class="iconfont iconservice"></i></div><span></span></li>
                <li><div><i class="iconfont iconmessage"></i></div><span></span></li>
                <li><div><i class="iconfont iconsetting"></i></div><span></span></li>
              </ul>
            </div>
          </div>
        </transition>
      </div>
    </transition>
