<script>
import { mapGetters } from 'vuex'
import startPoint from '@/assets/start_point.png'
import endPoint from '@/assets/end_point.png'
export default {
  data () {
    return {
      amap: null,
      startPoint: null,
      startAddr: '',
      endAddr: '',
      showAutocomplete: false,
      destinationList: [],
      currentPointInfo: null,
      endPointInfo: null,
      endPoint: null,
      drivingPois: [], // 
      carMarker: null,
      polylines: null,
      passedPolylines: null,
      showSidebarNav: false,
      ws: null,
      on: true
    }
  },
  computed: {
    ...mapGetters([
      'getUserSocket',
      'getUserMsg',
      'getUserSocketStatus'
    ])
  },
  created () {
    const me = this
    this.$store.dispatch('creatUserWS').then((ws) => {
      this.ws = ws
      ws.onmessage = function (res) {
        const msg = JSON.parse(res.data)
        if (msg.type === '') {
          me.$hideLoading()
          me.on = false
        } else if (msg.type === '') { // 
          me.$showToast('', 3000)
        } else if (msg.type === '') {
          me.createEndPoint()
          me.startPoint.setAnimation('AMAP_ANIMATION_NONE')
        } else if (msg.type === '') {
          me.$showMessage({
            content: '22.1',
            showCancel: false,
            confirmCallBack: function () {
              me.on = true
              me.ws.send(JSON.stringify({
                to: 2,
                type: '',
                data: {}
              }))
            }
          })
        }
      }
    }).catch(() => {
      this.$showToast('', 2000)
    })
  },
  mounted () {
    const me = this
    me.amap = new AMap.Map('container', {
      zoom: 15,
      resizeEnable: false,
      dragEnable: false
    })
    me.createStartPoint().then(marker => {
      me.getCurrentPosition()
        .then(res => {
          marker.setPosition(res.position)
          me.startAddr = res.formattedAddress
          me.currentPointInfo = res
          me.getDestinationList().then(pois => {
            me.destinationList = pois.poiList.pois
          })
        }).catch(() => {
          const res = { 'position': { 'P': 40.00009765625, 'Q': 116.48696777343798, 'lng': 116.486968, 'lat': 40.000098 }, 'location_type': 'html5', 'message': 'Get geolocation success.Convert Success.Get address success.', 'accuracy': 64, 'isConverted': true, 'status': 1, 'addressComponent': { 'citycode': '010', 'adcode': '110105', 'businessAreas': [{ 'name': '', 'id': '110105', 'location': { 'P': 39.996171, 'Q': 116.47029299999997, 'lng': 116.470293, 'lat': 39.996171 } }], 'neighborhoodType': '', 'neighborhood': '', 'building': '', 'buildingType': '', 'street': '', 'streetNumber': '1', 'province': '', 'city': '', 'district': '', 'township': '' }, 'formattedAddress': '.A', 'roads': [], 'crosses': [], 'pois': [], 'info': 'SUCCESS' }
          marker.setPosition(res.position)
          me.startAddr = res.formattedAddress
          me.currentPointInfo = res
          me.getDestinationList().then(pois => {
            me.destinationList = pois.poiList.pois
          })
          me.$showToast('', 2000)
        })
    })
  },
  methods: {
    // 
    getCurrentPosition () {
      const me = this
      return new Promise((resolve, reject) => {
        AMap.plugin('AMap.Geolocation', function () {
          const geolocation = new AMap.Geolocation({
            enableHighAccuracy: true,
            timeout: 10000,
            showButton: false,
            showCircle: false,
            showMarker: false,
            zoomToAccuracy: true
          })
          me.amap.addControl(geolocation)
          geolocation.getCurrentPosition(function (status, result) {
            if (status === 'complete') {
              resolve(result)
            } else {
              reject(result)
            }
          })
        })
      })
    },
    // 
    createStartPoint () {
      const me = this
      return new Promise(resolve => {
        me.startPoint = new AMap.Marker({
          position: me.amap.getCenter(),
          icon: startPoint,
          offset: new AMap.Pixel(-13, -30)
          // draggable: true,
          // cursor: 'move'
        })
        me.startPoint.setMap(me.amap)
        me.startPoint.setLabel({
          offset: new AMap.Pixel(0, -42),
          content: '<div class="mark-label"></div>',
          direction: 'center'
        })
        me.startPoint.setAnimation('AMAP_ANIMATION_BOUNCE')
        resolve(me.startPoint)
      })
    },
    // 
    getDestinationList () {
      const me = this
      return new Promise((resolve, reject) => {
        const cityCode = me.currentPointInfo.addressComponent.citycode
        const position = me.currentPointInfo.position
        AMap.service(['AMap.PlaceSearch'], function () {
          const placeSearch = new AMap.PlaceSearch({
            type: '',
            pageSize: 30,
            pageIndex: 1,
            city: cityCode,
            citylimit: true
          })
          placeSearch.searchNearBy('', position, 5000, function (status, result) {
            if (status === 'complete') {
              resolve(result)
            } else {
              reject(result)
            }
          })
        })
      })
    },
    // 
    selectDestination (index) {
      this.endPointInfo = this.destinationList[index]
      this.endAddr = this.endPointInfo.address
      this.showAutocomplete = false
      this.$store.commit('SET_START_INFO', this.currentPointInfo)
      this.$store.commit('SET_END_INFO', this.endPointInfo)
      // this.createEndPoint()
      // this.startPoint.setAnimation('AMAP_ANIMATION_NONE')
    },
    // 
    callCar () {
      const msg = {
        to: 2,
        type: '',
        data: {
          startPoint: this.currentPointInfo,
          endPoint: this.endPointInfo
        }
      }
      this.ws.send(JSON.stringify(msg))
      this.$showLoading('')
    },
    // 
    createEndPoint () {
      const me = this
      return new Promise(resolve => {
        if (me.endPoint) {
          me.endPoint.setPosition(me.endPointInfo.location)
          if (me.polylines) {
            me.amap.remove(me.polylines)
          }
          if (me.passedPolylines) {
            me.amap.remove(me.passedPolylines)
          }
        } else {
          me.endPoint = new AMap.Marker({
            position: me.endPointInfo.location,
            icon: endPoint,
            offset: new AMap.Pixel(-13, -30)
          })
          me.endPoint.setMap(me.amap)
          me.endPoint.setLabel({
            offset: new AMap.Pixel(0, -42),
            content: '<div class="mark-label mark-label-red"></div>',
            direction: 'center'
          })
        }
        me.amap.setFitView()
        me.getDrivingPois().then(() => {
          // 
          me.creatCarMarker().then(marker => {
            marker.setMap(me.amap)
            me.polyline()
          })
        })
        resolve(me.endPoint)
      })
    },
    // 
    getDrivingPois () {
      const me = this
      return new Promise((resolve, reject) => {
        AMap.service('AMap.Driving', function () {
          const driving = new AMap.Driving({
            hideMarkers: true
          })
          driving.search(me.currentPointInfo.position, me.endPointInfo.location, function (status, result) {
            if (status === 'complete') {
              let drivingPois = []
              if (result.routes && result.routes[0] && result.routes[0].steps) {
                result.routes[0].steps.forEach(item => {
                  drivingPois = drivingPois.concat(item.path)
                })
              }
              me.drivingPois = drivingPois
              resolve(drivingPois)
            } else {
              reject(result)
            }
          })
        })
      })
    },
    // 
    creatCarMarker () {
      const me = this
      return new Promise(resolve => {
        if (me.carMarker) {
          me.carMarker.setPosition(me.currentPointInfo.position)
        } else {
          me.carMarker = new AMap.Marker({
            position: me.currentPointInfo.position,
            icon: 'https://webapi.amap.com/images/car.png',
            offset: new AMap.Pixel(-26, -13),
            autoRotation: true,
            angle: -90
          })
        }
        resolve(me.carMarker)
      })
    },
    // 
    polyline () {
      const me = this
      return new Promise(resolve => {
        me.polylines = new AMap.Polyline({
          map: me.amap,
          path: me.drivingPois,
          showDir: true,
          strokeColor: '#28F',
          strokeWeight: 6
        })

        me.passedPolylines = new AMap.Polyline({
          map: me.amap,
          strokeColor: '#AF5',
          strokeWeight: 6
        })
        me.carMarker.on('moving', function (e) {
          me.passedPolylines.setPath(e.passedPath)
        })
        me.amap.setFitView()
        me.carMarker.moveAlong(me.drivingPois, 200)
      })
    }
  }
}
</script>
