<template>
  <div class="home">
    <div class="user-header">
      <div class="user">
        <button @click="showSidebarNav = true"><i class="iconfont iconuser"></i></button>
      </div>
      <div class="title">Online Taxi Driver</div>
      <div class="tv">
        <button><i class="iconfont icontv"></i></button>
      </div>
    </div>
    <div class="action">
      <div class="d-order-info">
        <h2>2.8 32</h2>
        <ul>
          <li>
            <i></i>
            <span class="label">:</span>
            <span class="value">{{startPoint.formattedAddress}}</span>
          </li>
          <li>
            <i></i>
            <span class="label">:</span>
            <span class="value">{{endPoint.address}}</span>
          </li>
        </ul>
      </div>
      <button @click="pick"></button>
    </div>
  </div>
</template>
<script>
export default {
  data () {
    return {
      ws: null,
      startPoint: {},
      endPoint: {}
    }
  },
  created () {
    const me = this
    this.$store.dispatch('creatDriverWS').then((ws) => {
      this.ws = ws
      ws.onmessage = function (res) {
        const msg = JSON.parse(res.data)
        if (msg.type === '') {
          me.startPoint = msg.data.startPoint
          me.endPoint = msg.data.endPoint
          me.$store.commit('SET_START_INFO', msg.data.startPoint || {})
          me.$store.commit('SET_END_INFO', msg.data.endPoint || {})
        }
      }
      this.$store.commit('SET_DRIVER_SOCKET', ws)
    }).catch(() => {
      this.$showToast('', 2000)
    })
  },
  methods: {
    pick () {
      this.ws.send(JSON.stringify({
        to: 1,
        type: '',
        data: {}
      }))
      this.$router.push('/driver/ing')
    }
  }
}
</script>
<style scoped>
.home {
  background-color: #f8f8f8;
}
.action {
  width: 100%;
  height: auto;
  position: absolute;
  left: 0;
  bottom: 0;
  padding-bottom: 100px;
}
.action button {
  width: 120px;
  height: 120px;
  display: block;
  border-radius: 100%;
  margin: 0 auto;
  border: 0;
  cursor: pointer;
  background-color: #00b48d;
  color: #ffffff;
  font-size: 32px;
  outline: none;
}
.d-order-info {
  width: 80%;
  border: 1px solid #00b48d;
  margin: 0 auto 40px;
  box-shadow: 0 0 5px 0 #00b48d;
  background-color: #ffffff;
  border-radius: 2px;
}
.d-order-info h2 {
  background-color: #00b48d;
  color: #ffffff;
  margin: 0;
  padding: 15px;
  line-height: 1.5;
  font-weight: normal;
  font-size: 18px;
  text-align: left;
}
.d-order-info ul {
  text-align: left;
  padding: 15px;
  font-size: 16px;
}
.d-order-info ul li {
  display: flex;
  align-items: center;
  padding: 10px 0;
}
.d-order-info ul li i {
  display: block;
  width: 8px;
  height: 8px;
  border-radius: 100%;
  background-color: #00b48d;
  margin: 0 15px;
}
.d-order-info ul li:nth-child(2) i {
  background-color: palevioletred;
}
.d-order-info ul li .value {
  flex: 1;
  padding-left: 3px;
}
</style>
