<template>
  <div class="home">
    <div class="user-header">
      <div class="user">
        <button @click="showSidebarNav = true"><i class="iconfont iconuser"></i></button>
      </div>
      <div class="title"></div>
      <div class="tv">
        <button><i class="iconfont icontv"></i></button>
      </div>
    </div>
    <pay-detail></pay-detail>
    <div class="pay-btn">
      <button type="button" class="btn-primary" @click="pay"></button>
    </div>
  </div>
</template>
<script>
import { mapGetters } from 'vuex'
import PayDetail from '@/components/PayDetail.vue'
export default {
  components: {
    PayDetail
  },
  computed: {
    ...mapGetters({
      ws: 'getDriverSocket'
    })
  },
  mounted () {
    const ws = this.ws
    const me = this
    ws.onmessage = function () {
      me.$showMessage({
        content: '22.1',
        showCancel: false,
        confirmCallBack: function () {
          me.$router.push('/driver/on')
        }
      })
      me.$router.push('/driver/on')
    }
  },
  methods: {
    pay () {
      const ws = this.ws
      ws.send(JSON.stringify({
        to: 1,
        type: '',
        data: {}
      }))
    }
  }
}
</script>
<style scoped>
.pay-btn { width: 100%; position: absolute; left: 0; bottom: 0; box-sizing: border-box; padding: 15px; }
</style>
